import QSTabs from '../../../components/QS-tabs/QS-tabs.vue';


import {
	mapState
} from 'vuex'

let _this = this;
let loginResult;

export default {
	
	
	components: {
		QSTabs
	},
	data() {
		return {
			title: 'Hello',
			br:'\n',
			tabs: ['作品','相册','文章'],
			current: 0,
			tabsHeight: 0,
			dx: 0,
			nickname:'昵称',
			face:'',
			uid:'',
			// isLogin1:false,
			
			works:{},
			images:[]
			
			
		}
	},
	
	onLoad() {
		loginResult = this.checkLogin('/pages/tabbar-5/tabbar-5', 0);
		if(!loginResult){return;}
	},
	
	onShow() {
		let userinfo = uni.getStorageSync('userinfo');
		console.log('userinfo:');
		console.log(userinfo);
		
		this.nickname = userinfo.nickname;
		this.face = userinfo.headPortraitImg;
		this.uid = userinfo.id;
		
		this.getWorksByUserId();
		this.getImagesByUserId();
		
		
		
	},
	
	methods: {
		
		_gotoPage:function(pageUrl){
			this.gotoPage(pageUrl);
		},
		change(index) {
			this.current = index;
		},
		swiperChange({detail: { current }}) {
			this.current = current;
		},
		transition({detail: { dx }}) {
			this.$refs.tabs.setDx(dx);
		},
		animationfinish({detail: { current }}) {
			this.$refs.tabs.setFinishCurrent(current);
		},
		isLogin(){
			var SNAME = uni.getStorageSync('SNAME');
			if(SNAME != ''){
				return true;
			}else{
				return false;
			}
		},
		getWorksByUserId:function(){
			uni.request({
				url:this.createApiUrl('/works/get/uid'),
				data:{
					uid:this.uid
				},
				success: (res) => {
					this.works = res.data.data;
					console.log(this.works);
				}
			})
		},
		
		
		getImagesByUserId:function(){
			uni.request({
				url:this.createApiUrl('user/images'),
				data:{
					uid:this.uid
				},
				success: (res) => {
					console.log(res);
					this.images = res.data.data;
					console.log(this.images);
					
				}
			})
		},
		

	}
}
